import { Icon } from '@cetus/ui-kit'
import { Button } from '@chakra-ui/react'

type ActionButtonProps = {
  type: 'Add' | 'Sub'
  onClick: (data?: any) => void
  disabled?: boolean
}
export default function ActionButton(props: ActionButtonProps) {
  const { type, onClick, disabled } = props
  return (
    <Button
      variant="unstyled"
      display="flex"
      justifyContent="center"
      alignItems="center"
      minW={{ base: '32px', lg: '24px' }}
      w={{ base: '32px', lg: '24px' }}
      h={{ base: '32px', lg: '24px' }}
      bg="bg_secondary"
      border="1px solid"
      borderColor="border"
      borderRadius="8px"
      lineHeight="1"
      cursor="pointer"
      onClick={onClick}
      isDisabled={disabled}
      _hover={{
        color: 'text_caption',
        svg: {
          fill: 'text_caption'
        }
      }}
    >
      <Icon xlinkHref={type === 'Add' ? '#icon-a-icon_add1' : '#icon-tx_remove'} fontSize="14px" />
    </Button>
  )
}
